<template>
    <div>
        <TopLink></TopLink>
        <div class="clear"></div>
        <img src="images/timg.gif" v-if="bookData.length==0" class="loding">
        <div v-else>
            <bookList title="最受关注图书 | 虚构类" :mlData="bookArra" :url="url"></bookList>
            <bookList title="最受关注图书 | 非虚构类" :mlData="bookArrb" :url="url"></bookList>
            <bookList title="豆瓣纸书" :mlData="bookArrc" :url="url" :topBook="bookArrd"></bookList>
            <Fxhdy title="发现好书"></Fxhdy>
            <Sort title="分类浏览" :sorts="sorts"></Sort>
        </div>
    </div>
</template>
<script>
import TopLink from "@/components/toplink/toplink.vue"
import bookList from "@/components/movieList/movieList.vue"
import Fxhdy from "@/pages/movie/fxhdy/fxhdy.vue"
import Sort from "@/pages/movie/sort/sort.vue"
export default {
    data(){
        return{
            bookData:[],
            url:"/book",
            sorts:[],
        }
    },
    components:{
        TopLink,bookList,Fxhdy,Sort
    },
    mounted() {
        this.axios({
            url:"/api/book/books",
            method:"get",
        }).then((res)=>{
            this.bookData = res.data.data;
            this.bookData.forEach(v=>{
                v.rating.average = v.rating.average/2
            });
        });
        this.axios({
            url:"/api/movie/sorts",
            method:"get",
        }).then((res)=>{
            this.sorts = res.data.data;
        });
    },
    computed:{
        bookArra(){
            return this.bookData.filter(v=>{
                if(v.tags[1].name){
                    if(v.tags[1].name.length>=3){
                        return v.tags[1].name.slice(0,3) != "非虚构"
                    }else{
                        return v.tags[1].name.slice(0,v.tags[1].name.length) != "非虚构"
                    }
                }
            }).slice(0,6)
        },
        bookArrb(){
            return this.bookData.filter(v=>{
                if(v.tags[1].name){
                    if(v.tags[1].name.length>=3){
                        return v.tags[1].name.slice(0,3) == "非虚构"
                    }else{
                        return v.tags[1].name.slice(0,v.tags[1].name.length) == "非虚构"
                    }
                }
            }).slice(0,6)
        },
        bookArrc(){
            return this.bookData.filter(v=>{
                if(v.tags[1].name){
                    if(v.tags[1].name.length>=3){
                        return v.tags[1].name.slice(0,3) != "虚构"&& v.tags[1].name.slice(0,3) != "非虚构"
                    }else{
                        return v.tags[1].name.slice(0,v.tags[1].name.length) != "虚构"&& v.tags[1].name.slice(0,v.tags[1].name.length) != "非虚构"
                    }
                }
            }).slice(7,13)
        },
        bookArrd(){
            return this.bookData.filter(v=>{
                return v.rating.average>=4.5
            })
        }
    }
}
</script>
